/* Page One */
.page-one {
  display: flex;
  justify-content: flex-start;
  padding: 11%;
  width: 100%;
}

.intro-container {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: 10%;
}

.name-container {
  display: flex;
  transition: transform 0.3s ease-out;
}

.name-container h1 {
  font-family: Palatino Bold;
  font-size: 150rem;
  letter-spacing: 5rem;
  color: #eeb669;
  text-align: left;
}

.last-name {
  /* position: absolute;
  left: 5%;
  top: 0; */
  font-family: Palatino Bold;
  font-size: 150rem;
  letter-spacing: 5rem;
  text-align: left;
  margin-left: 50rem;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3rem;
  -webkit-text-stroke-color: #eeb669;
  z-index: 2;
}

.img {
  position: absolute;
  right: 0;
  top: 0;
  overflow: hidden;
}

@media only screen and (min-width: 1200px) {
  .img {
    right: 0;
  }
}
.img img {
  width: 23vw;
  opacity: 0.5;
}

@media only screen and (min-width: 1200px) {
  .img img {
    width: 17vw;
  }
}
.img .gradient {
  background: linear-gradient(#0000001f, #dfb58410);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 150%;
}

.greeting {
  font-size: 20rem;
  line-height: 30rem;
  color: #a8a5a3;
  font-weight: 200;
  margin-top: 45rem;
  margin-left: 25rem;
  transition: transform 0.3s ease-in-out;
}

.greeting span {
  color: #eeb669;
}

.explore-container {
  display: flex;
  margin-left: 25rem;
  margin-top: 100rem;
  cursor: pointer;
}

.explore-container p {
  font-weight: 600;
  color: #f2f2f2;
  font-size: 25rem;
  margin-right: 20rem;
}

.explore-container:hover p {
  color: #c97328;
}

.explore-container .triangle {
  width: auto;
  height: 15rem;
  align-self: center;
}
